import { PublicKey } from "@solana/web3.js";
import { Address } from "@project-serum/anchor";
import { create_fetcher } from "./client";

type WhirlpoolInfo = {
  address: PublicKey,
  config: PublicKey,
}


export async function get_whirlpool_info(pubkey: Address): Promise<WhirlpoolInfo> {
  const fetcher = create_fetcher();

  const whirlpool = await fetcher.getPool(pubkey);
  return {
    address: new PublicKey(pubkey),
    config: whirlpool.whirlpoolsConfig,
  };
}