import { Connection } from "@solana/web3.js";
import { AccountFetcher } from "@orca-so/whirlpools-sdk";

let _connection: Connection;

export function set_rpc(rpc: string) {
  _connection = new Connection(rpc, "confirmed");
}

export function get_connect(): Connection {
  return _connection;
}

export function create_fetcher(): AccountFetcher {
  return new AccountFetcher(_connection);
}
